// Generate achievements for each terrain detail
const generateTerrainAchievements = () => {
    const achievements = {};

    Object.keys(TERRAIN_DETAILS).forEach(detailKey => {
        const detail = TERRAIN_DETAILS[detailKey];
        const achievementName = detail.name;

        // Achievement for finding and enlarging 1 item (listed first)
        achievements[`enlarge1_${detailKey}`] = {
            name: `Grow 5 ${achievementName}`,
            unlocked: false,
            progress: 0,
            progressGoal: 5,
            description: `Find and grow 5 ${achievementName}.`,
            reward: 50 // Adjust reward as needed
        };


    });
    Object.keys(TERRAIN_DETAILS).forEach(detailKey => {
        const detail = TERRAIN_DETAILS[detailKey];
        const achievementName = detail.name;

        // Achievement for finding and enlarging 20 items (listed after)
        achievements[`enlarge20_${detailKey}`] = {
            name: `Grow 50 ${achievementName}`,
            unlocked: false,
            progress: 0,
            progressGoal: 50,
            description: `Find and grow 50 ${achievementName}.`,
            reward: 500 // Adjust reward as needed
        };


    });



    return achievements;
};
const initialSpeedStats = {
    swimming: 0,
    climbing: 0.3,
    running: 1,
    trekking: 0.7
};
const terrainMovementTypes = {
    swimming: ['LAKE', 'DEEP LAKE'],
    climbing: ['MOUNTAIN', 'MOUNTAINPEAK'],
    running: ['GRASS', 'SAND', "DIRT", "ICE", "SNOWY GRASS", "SAVANNA", "OASIS", "SANDY SAVANNA"],
    trekking: ['FOREST', 'SNOW', "SNOW FOREST",]
};

const generateSpeedAbilities = () => {
    const abilities = {};

    Object.keys(initialSpeedStats).forEach(type => {
        abilities[`${type}MinorBoost1`] = { name: `Increase ${type} speed by 0.1`, unlocked: false, price: type === 'swimming' ? 100 : 50, description: `Increase ${type} speed by 0.1` };
        abilities[`${type}MinorBoost2`] = { name: `Increase ${type} speed by 0.1`, unlocked: false, price: type === 'swimming' ? 100 : 50, description: `Increase ${type} speed by 0.1` };
        abilities[`${type}MajorBoost1`] = { name: `Increase ${type} speed by 0.5`, unlocked: false, price: type === 'swimming' ? 500 : 200, description: `Increase ${type} speed by 0.5` };
        abilities[`${type}MajorBoost2`] = { name: `Increase ${type} speed by 0.5`, unlocked: false, price: type === 'swimming' ? 500 : 200, description: `Increase ${type} speed by 0.5` };
    });

    return abilities;
};

