export function createPlayerState(playerStateRef, setPlayerState, player, setIsMenuOpen) {
    let modeIndex = 1;
    const allModes = ['Passive', 'Grow', 'Free', 'Destroy'];

    const modeColors = {
        Passive: config.PLAYER_BASE_COLOR,
        Grow: config.PLAYER_BASE_COLOR,
        Free: config.PLAYER_BASE_COLOR,//"#050996",
        Destroy: "#940721",
    };

    const setPlayerStateInternal = (newState) => {
        if (JSON.stringify(playerStateRef.current) !== JSON.stringify(newState)) {
            playerStateRef.current = newState; // Update the ref
            setPlayerState(newState); // Update the state
            updatePlayerColor(player, newState.color); // Update player color
            console.log('Player state updated:', newState);
        }
    };
    const updatePlayerStateInternal = (newState) => {
        let updatedState = { ...playerStateRef.current, ...newState };

        // Check if any achievements have been unlocked
        updatedState.achievements = checkAchievements(updatedState);

        // Update the player state with the new achievements and XP
        setPlayerStateInternal(updatedState);
    };


    const getTerrainSpeedAdjustment = (terrainType) => {
        const { abilities } = playerStateRef.current;
        const speedStats = { ...initialSpeedStats }; // Initialize with default speeds

        // Apply unlocked abilities to improve speed stats
        Object.keys(speedStats).forEach(type => {
            if (abilities[`${type}MinorBoost1`]?.unlocked) speedStats[type] += 0.1;
            if (abilities[`${type}MinorBoost2`]?.unlocked) speedStats[type] += 0.1;
            if (abilities[`${type}MajorBoost1`]?.unlocked) speedStats[type] += 0.5;
            if (abilities[`${type}MajorBoost2`]?.unlocked) speedStats[type] += 0.5;
        });

        // Exception for LAKE: always 0.5 higher than swimming speed
        if (terrainType === 'LAKE') {
            return speedStats['swimming'] + 0.5;
        }

        // Adjust speed based on terrain
        for (const [type, terrains] of Object.entries(terrainMovementTypes)) {
            if (terrains.includes(terrainType)) {
                return speedStats[type]; // Return the corresponding speed stat
            }
        }

        return 1; // Default speed
    };
    const getBlockedTerrain = () => {
        const blockedTerrains = [];

        Object.keys(terrainMovementTypes).forEach(type => {
            terrainMovementTypes[type].forEach(terrain => {
                const terrainSpeed = getTerrainSpeedAdjustment(terrain);
                if (terrainSpeed <= 0) {
                    blockedTerrains.push(terrain); // Block terrain if speed is <= 0
                }
            });
        });

        return blockedTerrains;
    };


    const setMode = (newMode) => {
        if (allModes.includes(newMode)) {
            const newColor = modeColors[newMode];
            updatePlayerStateInternal({ mode: newMode, color: newColor });
        }
    };

    const cycleMode = () => {
        modeIndex = (modeIndex + 1) % allModes.length;
        setMode(allModes[modeIndex]);
        return allModes[modeIndex];
    };




    const handleTerrainInteraction = (tileX, tileY, caches) => {
        let xpGain = 0;

        switch (playerStateRef.current.mode) {
            case 'Grow':
            case 'Free': {
                const enlargedDetails = enlargeTerrainDetail(tileX, tileY, caches);
                if (enlargedDetails) {
                    xpGain += enlargedDetails.length;

                    enlargedDetails.forEach(({ detailKey }) => {
                        // Replace with the new function to track and update achievements
                        updateEnlargedDetailProgress(detailKey, playerStateRef);
                    });
                }
                break;
            }
            case 'Destroy': {
                const removedDetails = removeTerrainDetail(tileX, tileY, caches);
                if (removedDetails) {
                    xpGain += removedDetails.length;

                    removedDetails.forEach(({ detailKey }) => {
                        // Use detailKey to track deleted terrain details in the state
                        const currentDeletedCount = playerStateRef.current.deletedDetails[detailKey] || 0;
                        playerStateRef.current.deletedDetails[detailKey] = currentDeletedCount + 1;
                    });
                }
                break;
            }
            case 'Passive':
                break;
            default:
                console.warn('Unknown player mode');
        }

        if (xpGain > 0) {

            // updatePlayerStateInternal({
            //     xp: (playerStateRef.current.xp || 0) + xpGain
            // });
        }
    };



    const purchaseAbility = (abilityKey) => {
        const ability = playerStateRef.current.abilities[abilityKey];

        if (ability && !ability.unlocked && playerStateRef.current.xp >= ability.price) {
            const updatedXp = playerStateRef.current.xp - ability.price;

            // Unlock the ability
            const updatedAbilities = { ...playerStateRef.current.abilities, [abilityKey]: { ...ability, unlocked: true } };

            let newMode = playerStateRef.current.mode;

            // If the purchased ability is Free Mode, set the mode to "Free"
            if (abilityKey === 'freeModeAccess') {
                newMode = 'Free';
            }

            // Update the player state
            updatePlayerStateInternal({
                xp: updatedXp,
                abilities: updatedAbilities,
                mode: newMode,
                color: modeColors[newMode], // Update color if mode changes
            });

            // Check if the purchased ability is related to movement speed
            Object.keys(initialSpeedStats).forEach(type => {
                if (abilityKey.includes(type)) {
                    console.log(`Speed ability purchased: ${ability.name}`);
                    // Optional: trigger any special behavior or animations when speed abilities are purchased
                }
            });

            console.log(`Ability purchased: ${ability.description}`);
        } else {
            console.log('Not enough XP to purchase this ability.');
        }
    };


    // Open menu on player click
    const openMenuOnClick = () => {
        setIsMenuOpen(true); // Toggle the menu visibility
    };

    const closeMenu = () => {
        setIsMenuOpen(false); // Close the menu
    };

    return {
        playerState: playerStateRef.current,
        allModes,
        setPlayerState: setPlayerStateInternal,
        setMode,
        purchaseAbility,
        updatePlayerStateInternal,
        cycleMode,
        openMenuOnClick, // Add the openMenuOnClick function to the player object
        closeMenu,       // Add closeMenu to playerState
        getBlockedTerrain,
        handleTerrainInteraction,
        getTerrainSpeedAdjustment,
    };
}



const updateEnlargedDetailProgress = (detailKey, playerStateRef) => {
    // Track enlarged detail count
    const currentEnlargedCount = playerStateRef.current.enlargedDetails[detailKey] || 0;
    playerStateRef.current.enlargedDetails[detailKey] = currentEnlargedCount + 1;

    // Ensure achievements are initialized
    const achievements = playerStateRef.current.achievements || {};

    // Update the achievement for finding and enlarging 1 item
    const enlarge1Key = `enlarge1_${detailKey}`;
    const enlarge1Achievement = achievements[enlarge1Key];
    if (enlarge1Achievement && !enlarge1Achievement.unlocked) {
        enlarge1Achievement.progress += 1;

    }

    // Update the achievement for finding and enlarging 20 items
    const enlarge20Key = `enlarge20_${detailKey}`;
    const enlarge20Achievement = achievements[enlarge20Key];
    if (enlarge20Achievement && !enlarge20Achievement.unlocked) {
        enlarge20Achievement.progress += 1;
    }
};
//Checking achievements (can be expanded to check all terrain-related achievements)
const checkAchievements = (playerState) => {
    const newAchievements = { ...playerState.achievements };
    // Iterate through terrain achievements and check if they've been unlocked
    Object.keys(TERRAIN_DETAILS).forEach(detailKey => {
        const enlarge1Key = `enlarge1_${detailKey}`;
        const enlarge20Key = `enlarge20_${detailKey}`;
        // Check if the player has enlarged 1 of the terrain detail
        if (!newAchievements[enlarge1Key]?.unlocked && newAchievements[enlarge1Key]?.progress >= newAchievements[enlarge1Key]?.progressGoal) {
            toast(`Achievement Unlocked: ${newAchievements[enlarge1Key].name}`)
            newAchievements[enlarge1Key].unlocked = true;


            playerState.xp += newAchievements[enlarge1Key].reward;
            console.log(`Achievement Unlocked: ${newAchievements[enlarge1Key].name}`);

        }

        // // Check if the player has enlarged 20 of the terrain detail
        // if (!newAchievements[enlarge20Key].unlocked && newAchievements[enlarge20Key].progress >= newAchievements[enlarge20Key].progressGoal) {
        //     toast(`Achievement Unlocked: ${newAchievements[enlarge20Key].name}`)
        //     newAchievements[enlarge20Key].unlocked = true;
        //     playerState.xp += newAchievements[enlarge20Key].reward;
        //     console.log(`Achievement Unlocked: ${newAchievements[enlarge20Key].name}`);

        // }
    });

    return newAchievements;
};
export function updatePlayerColor(player, newColor) {
    player.layers.forEach((layer, index) => {
        // Reset the layer's scale to its original size (base size)
        const originalSize = config.PLAYER_MAX_SIZE - (index * config.PLAYER_MAX_SIZE) / config.PLAYER_LAYER_COUNT;

        // Set the scale of the layer to match the original size
        layer.sprite.scale.set(1); // Reset scale to 1 before updating the texture

        // Calculate the alpha based on the index, just like during the layer creation
        const alpha = 0.05 + index * 0.05;

        // Create a new texture with the correct size and alpha
        const texture = createRadialGradientTexture(originalSize, parseColorString(newColor), alpha);
        layer.sprite.texture = texture;
    });
}
export function canMoveOnTerrain(player, terrainType) {
    return !player.getBlockedTerrain().includes(terrainType);
}
